@import "partials/colors";
@import "partials/shared";

@import "partials/site/header";
@import "partials/site/social-media-links";
@import "partials/site/intro";
@import "partials/site/about";
@import "partials/site/skills";
@import "partials/site/experience";
@import "partials/site/projects";
@import "partials/site/contact";
@import "partials/site/footer";

html {
  scroll-behavior: smooth;
}

body {
  background: $base-color;
  font-family: "Helvetica", Times, serif;
  color: $font-primary_light;
  padding: 12pt 40pt 0pt 40pt;
}

a {
  color: $font-primary_light;
}

.terminal {
  border-image-source: url(../images/terminal.dark.png);
  border-image-slice: 150 150 150 150;
}

.btn {
  padding: 10px 12px 10px 12px;
  color: $accent-color;
  border: 1px solid $accent-color;
  border-radius: 4px;

  &:hover {
    background: transparentize($color: $accent-color, $amount: 0.75);
  }
}

ul {
  list-style: none;
}

ul li::before {
  content: "\2022";
  color: $accent-color;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__header {
    font-size: 20pt;
    color: $font-primary_light;
    padding: 32pt;

    * {
      display: inline;
    }
    &__index {
      color: $accent-color
    }
  }

  &__content {
    max-width: 750px;
    width: 75vw;
  }
}